//数据模拟
import data from './list.js';
export default {
	props: {
		//评价列表数据
		listData: {
			type: Array,
			default: data
		},
		//是否显示总评价数量
		isShowTotal: {
			type: Boolean,
			default: true
		},
		//是否显示评价前面的图标
		isShowIcon: {
			type: Boolean,
			default: true
		},
		//总评分
		rate: {
			type: Number,
			default: 4.6
		}
	},
	data() {
		return {}
	},
	methods: {
		previewImg(src,urls){
			uni.previewImage({
				current:src,
				urls
			})
		}
	}
};
